@import "vars";
@import "mixins";
@import "fonts";
@import "settings";


.all_pages{
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover{
    ul{
      right: 0;
    }
  }
  .page_point{
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul{
    background: #00D2FF;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: .4s;
    max-height: 450px;
    overflow-y: auto;
    li{
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a{
        color: #fff;
      }
    }
  }
}

// HEADER START ========================================================================================================================================================
.header-profile {
  .header__login {
    display: none;
    @media (max-width: 768px){
        display: block;
    }
  }
  .header__profile-info {
    display: flex !important;
    justify-content: center;
    align-items: center;
    column-gap: 15px;
  }
  .header__profile-avatar {
    width: 40px;
    height: 40px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .header__profile-name {
    padding-right: 15px;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    color: var(--dark-color);
    position: relative;
    transition: all 0.3s ease 0s;
    transform: translateY(1px);
    @media (max-width: 992px){
        padding-right: 0;
    }
    @media (max-width: 576px){
        display: none;
    }
    &:hover {
      color: var(--accent-color);
    }
    &:hover:after {
      border-color: 2px solid var(--accent-color);
    }
    &:after {
      content: '';
      position: absolute;
      right: 5px;
      top: 5px;
      transform-origin: 50% 75%;
      transform: rotate(45deg);
      width: 6px;
      height: 6px;
      background: 2px solid var(--dark-color);
      border: 2px solid var(--dark-color);
      border-left: none;
      border-top: none;

      transition: all 0.3s ease 0s;
      @media (max-width: 1400px){
          right: 5px;
      }
      @media (max-width: 1200px){
        top: 6px;
      }
      @media (max-width: 992px){
          width: 8px;
          height: 8px;
          right: -20px;
          top: 3px;
          transition-duration: 0s;
      }
      @media (max-width: 576px){
        width: 6px;
        height: 6px;
        top: 8px;
        right: -10px;
      }
    }
    &.active {
      &:after {
        @media (max-width: 480px){
          right: -15px;
        }
      }
    }
    + ul {
      padding: 0;
      margin: 0;
      list-style: none;
      position: absolute;
      right: 0;
      top: calc(100% + 10px);
      background-color: var(--light-color);
      box-shadow: 0px 10px 30px rgba(170, 201, 243, 0.9);
      padding: 15px;
      border-radius: 5px;
      visibility: hidden;
      opacity: 0;
      z-index: -1;
      transition: all 0.3s ease 0s;
      > li {
        margin-bottom: 5px !important;
        &:last-child {
          margin-bottom: 0;
        }
        @media (max-height: 400px){
            margin-bottom: 0 !important;
        }
        > a {
          display: inline-block;
          font-style: normal;
          font-size: 13px;
          font-weight: 700;
          line-height: 20px;
          text-transform: uppercase;
          color: var(--dark-color);
          position: relative;
          transition: all 0.3s ease 0s;
          &:hover {
            color: var(--accent-color);
            @media (max-width: 992px){
              color: var(--dark-color);
            }
          }
          @media (max-width: 1200px){
              font-size: 14px;
          }
          @media (max-width: 480px){
              font-size: 12px;
          }
          @media (max-height: 400px){
              font-size: 10px;
          }
        }
      }
      @media (max-height: 400px){
          overflow: auto;
      }
    }
    &.active {
      + ul {
        visibility: visible;
        opacity: 1;
        z-index: 10;
        @media (max-width: 992px){
          display: block;
          > li {
            margin-bottom: 0;
            line-height: 110%;
          }
        }
      }
      &:after {
        transform: rotate(225deg);
      }
    }
  }
  .search-icon {
    right: 143px;
    @media (max-width: 992px){
      right: 200px;
    }
    @media (max-width: 576px){
      right: 105px;
    }
  }
}

.header-white {
  .header {
    background-color: var(--light-color);
  }
}
.header {
  padding: 40px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  transition: all 0.3s ease 0s;
  @media (max-width: 992px){
      padding: 20px 0;
  }
  // .header__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 101;
    @media (max-width: 576px){
        img {
          max-width: 120px;
        }
    }
  }

  // .header__navigation

  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    column-gap: 40px;
    @media (max-width: 768px){
      column-gap: 35px;
      justify-content: flex-end;
    }
    @media (max-width: 576px){
      column-gap: 20px;
    }
    nav {
      transition: all 0.3s ease 0s;
      > ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        > li {
          &:nth-child(2) {
            transform: translateY(2.5px);
            font-style: normal;
            font-size: 14px !important;
            font-weight: 700;
            line-height: 20px;
            text-transform: uppercase;
            color: var(--dark-color);
            transition: all 0.3s ease 0s;
            position: relative;
            cursor: pointer;
            padding-right: 15px;
            @media (max-width: 1200px){
              transform: translateY(0);
            }
            @media (max-width: 992px){
              padding-right: 0;
            }
            &:after {
              content: '';
              position: absolute;
              right: 0px;
              top: 5px;
              transform-origin: 50% 75%;
              transform: rotate(45deg);
              width: 6px;
              height: 6px;
              background: 2px solid var(--dark-color);
              border: 2px solid var(--dark-color);
              border-left: none;
              border-top: none;

              transition: all 0.3s ease 0s;
              @media (max-width: 1200px){
                top: 8px;
              }
              @media (max-width: 992px){
                  width: 8px;
                  height: 8px;
                  right: -20px;
                  top: 3px;
                  transition-duration: 0s;
              }
              @media (max-width: 576px){
                width: 6px;
                height: 6px;
                top: 8px;
                right: -10px;
              }
            }
            &.active {
              &:after {
                @media (max-width: 480px){
                  right: -15px;
                }
              }
            }
          }
          > ul {
            padding: 0;
            margin: 0;
            list-style: none;
            position: absolute;
            top: calc(100% + 10px);
            background-color: var(--light-color);
            box-shadow: 0px 10px 30px rgba(170, 201, 243, 0.9);
            padding: 15px;
            border-radius: 5px;
            width: 160%;
            visibility: hidden;
            opacity: 0;
            z-index: -1;
            transition: all 0.3s ease 0s;
            @media (max-width: 992px){
                position: static;
                width: 100%;
                text-align: center;
                display: inline;
                padding: 0;
                display: none;
                background-color: transparent !important;
                box-shadow: none;
            }
            > li {
              margin-bottom: 5px !important;
              &:last-child {
                margin-bottom: 0;
              }
              @media (max-height: 400px){
                  margin-bottom: 0 !important;
              }
              > a {
                display: inline-block;
                font-style: normal;
                font-size: 13px;
                font-weight: 700;
                line-height: 20px;
                text-transform: uppercase;
                color: var(--dark-color);
                position: relative;
                transition: all 0.3s ease 0s;
                &:hover {
                  color: var(--accent-color);
                  @media (max-width: 992px){
                    color: var(--dark-color);
                  }
                }
                @media (max-width: 1200px){
                    font-size: 14px;
                }
                @media (max-width: 480px){
                    font-size: 12px;
                }
                @media (max-height: 400px){
                    font-size: 10px;
                }
              }
            }
            @media (max-height: 400px){
                overflow: auto;
            }
          }
          &.active {
            > ul {
              visibility: visible;
              opacity: 1;
              z-index: 10;
              @media (max-width: 992px){
                display: block;
                > li {
                  margin-bottom: 0;
                  line-height: 110%;
                }
              }
            }
            &:after {
              transform: rotate(225deg);
            }
          }
          @media (max-width: 1200px){
              font-size: 14px !important;
              line-height: 25px !important;
          }
          &:hover {
            &:after {
              border-color: var(--accent-color);
              @media (max-width: 992px){
                  border-color: var(--dark-color);
              }
            }
          }
        }
        @media (max-width: 992px){
            width: 100%;
            height: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            transform: translateY(-20px);
        }
        @media (max-height: 400px){
          transform: translateY(0px);
      }
        > li {
          margin-right: 25px;
          @media (max-width: 1200px){
              margin-right: 15px;
          }
          @media (max-width: 992px){
              margin-right: 0;
              margin-bottom: 10px;
          }
          @media (max-width: 576px) {
              margin-bottom: 5px;
          }
          @media (max-height: 400px){
              margin-bottom: 3px;
              text-align: center;
          }
          > a {
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 20px;
            text-transform: uppercase;
            color: var(--dark-color);
            transition: all 0.3s ease 0s;
            &:hover {
              color: var(--accent-color);
              @media (max-width: 992px){
                  color: var(--dark-color);
              }
            }
            &.active {
              color: var(--accent-color);
            }
            @media (max-width: 992px){
                font-size: 25px;
            }
            @media (max-width: 576px) {
                font-size: 18px;
            }
            @media (max-height: 400px){
              font-size: 16px;
            }
          }
        }
      }
      @media (max-width: 992px){
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100vh;
          z-index: 100;
          overflow-y: auto;
          transform: translateX(-100%);
          background: #EFF6FF;
          background: radial-gradient(circle, rgba(239,246,255,1) 0%, rgba(255,255,255,1) 35%, rgba(239,246,255,1) 70%);
      }
      @media (max-width: 768px){
          .header__login {
            position: absolute;
            bottom: 10%;
            left: 50%;
            transform: translateX(-50%);
            margin-left: 0;
          }
      }
      @media (max-height: 400px){
        .header__login {
          bottom: 5%;
          left: 90%;
          margin-left: 0;
        }
    }
    }
    .menu--active {
      transform: translateX(0%);
      + .search-icon {
        display: none;
      }
    }
    @media (max-width: 768px){
      .header__login {
        transform: scale(0);
      }
    }
  }

  // .header__login

  &__login {
    margin-left: 35px;
    padding-left: 35px;
    padding-right: 35px;
    @media (max-width: 1200px){
        margin-left: 20px;
    }
    @media (max-width: 768px){
        padding: 6px 20px;
        font-size: 12px;
        line-height: 20px;
    }
  }

  .burger {
    @include burger;
    display: none;
    @media (max-width: 992px){
        display: block;
        z-index: 101;
    }
  }
  &.scroll {
    background-color: rgba(#ffffff, 0.9);
    padding: 15px 0;
    box-shadow: 0 0 10px 5px rgba(0,0,0,0.2);
  }
  .header__profile-info {
    display: none;
  }
}
.search-icon {
  box-sizing: border-box;
  width: var(--icon-height);
  height: 36px;
  max-width: 710px;
  transition: all var(--transition-speed) linear, border-color 0s linear var(--transition-speed);
  position: absolute;
  top: 50%;
  right: 145px;
  transform: translateY(-50%);
  z-index: 50;
  background-color: transparent !important;
  border-radius: 5px;
  @media (max-width: 1200px) {
    right: 135px;
    max-width: 635px;
  }
  @media (max-width: 992px){
    right: 200px;
    max-width: 300px;
  }
  @media (max-width: 768px){
      right: 50px;
      z-index: 102;
  }
  @media (max-width: 576px){
    max-width: 250px;
  }
  @media (max-width: 400px){
    max-width: 190px;
  }
  &__wrapper {
    width: var(--icon-height);
    height: var(--icon-height);
    position: absolute;
    border-radius: 100px;
    top: 0;
    bottom:0;
    right: 0;
    margin: auto 0;
    transform: rotate(-45deg);
    transition: all 0 linear;
    &:hover {
      cursor: pointer;
    }
  }

  &__input {
    background: var(--whiteColor);
    outline: none;
    display: block;
    border: none;
    height: 36px;
    width: calc(100% - (var(--icon-height) / 2 + 1rem));
    margin-right: 6rem;
    transition: all var(--transition-speed) linear;
    color: var(--dark-color);
    transform: translateY(-3px);
    padding-left: 20px;
    padding-right: 20px;

    &::placeholder {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 13px;
      line-height: 20px;
      color: #808D94;
      text-align: left;

    }
  }

  input[type="submit"] {
    transform: scale(0);
  }
  &__glass {
    width: var(--glass-size);
    height: var(--glass-size);
    border: solid var(--line-thickness);
    border-color: var(--icon-color);
    border-radius: 100px;
    margin: 0 auto;
    position: relative;
    transition: all var(--transition-speed) var(--timing-function) var(--transition-speed), border-color 0s linear var(--transition-speed);
    @media (max-width: 480px) {
        width: 50%;
        height: 50%;
        position: absolute;
        top: 5px;
        left: 8px;
    }
  }

  &__handle {
    height: calc(100% - var(--glass-size));
    width: var(--line-thickness);
    margin: 0 auto;
    background: var(--icon-color);
    position: absolute;
    border-radius: 0 0 100px 100px;
    left: 0;
    right: 0;
    bottom: 0;
    transition: all var(--transition-speed) var(--timing-function);
    transition-delay: var(--transition-speed);

    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 80%;
      height: 100%;
      border-radius: inherit;
      background: var(--icon-color);
      transform: rotate(0deg);
      transition: all var(--transition-speed) var(--timing-function);
      transition-delay: 0s;
    }
  }

  &.open {
    background-color: var(--light-color) !important;
    box-shadow: 0px 10px 30px rgba(205, 227, 255, 0.5);
    width: calc(100vw - 1em);
    border-color: var(--icon-color);
    transition-delay: var(--transition-speed);
    border: 1px solid #CDE3FF;
    background-color: var(--light-color);
  }

  &.open &__input {
    transition-delay: var(--transition-speed);
  }

  &.open &__glass {
    width: 100%;
    height: 100%;
    transition: all var(--transition-speed) var(--timing-function) 0s, border-color 0s linear var(--transition-speed);
    border-color: rgba(0,0,0,0);
  }

  &.open &__handle {
    bottom: calc(50% - (100% - var(--glass-size)) / 2);
    border-radius: 100px;
    transition-delay: 0s;

    &::after {
      transition-delay: var(--transition-speed);
      transform: rotate(90deg)
    }
  }
}

