      counter-increment: ol-numbers;
      content: counter(ol-numbers);
      position: absolute;
      left: 40px;
      top: 50%;
      transform: translateY(-50%);
      color: var(--accent-color);
      font-style: normal;
      font-weight: 900;
      font-size: 24px;
