.exam {
  padding-top: 150px;
  padding-bottom: 150px;
  @media (max-width: 768px){
    padding-top: 120px;
    padding-bottom: 100px;
  }

  // .exam__subtitle

  &__subtitle {
    margin-top: 40px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: var(--dark-color);
    @media (max-width: 768px){
        font-size: 18px;
        line-height: 28px;
        margin-top: 25px;
    }
  }

  // .exam__container

  &__container {
    margin-top: 40px;
    background-color: var(--light-color);
    padding: 40px 40px 60px 40px;
    border: 1px solid #CDE3FF;
    border-radius: 10px;
    @media (max-width: 768px){
        padding: 20px 10px;
    }
  }

  // .exam__questions

  &__questions {
    display: flex;
    align-items: center;
    @media (max-width: 768px){
        align-items: flex-start;
    }
  }

  // .exam__btn-quest

  &__btn-quest,
  &__btn-posit {
    border: none;
    outline: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #EFF6FF;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 20px;
      height: 20px;
    }
    @media (max-width: 480px){
        width: 25px;
        height: 25px;
        img {
          width: 15px;
          height: 15px;
        }
    }
  }

  &__btn-quest {
    margin-right: 20px;
    @media (max-width: 768px){
        margin-right: 10px;
    }
    @media (max-width: 576px){
      min-width: 25px;
      height: 25px;
      img {
        width: 15px;
        height: 15px;
      }
    }
  }

  // .exam__fraction

  &__fraction {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 40px;
    color: var(--accent-color);
    margin-right: 10px;
    @media (max-width: 768px){
        font-size: 20px;
        line-height: 36px;
    }
    @media (max-width: 576px){
        font-size: 16px;
        line-height: 24px;
    }
  }

  // .exam__question

  &__question {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 40px;
    color: var(--dark-color);
    @media (max-width: 768px){
        font-size: 20px;
        line-height: 36px;
    }
    @media (max-width: 576px){
      font-size: 16px;
      line-height: 24px;
  }
  }

  // .exam__wrapper

  &__wrapper {
    margin-top: 40px;
    display: flex;
    column-gap: 30px;
    @media (max-width: 992px){
        flex-direction: column;
        row-gap: 40px;
    }
    @media (max-width: 576px){
        margin-top: 25px;
    }
  }

  // .exam__block

  &__block {
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .exam__asks {
      + .exam__buttons {
        button {
          &:hover {
            background-color: var(--accent-color);
            color: var(--light-color);
          }
        }
        button:first-child {
          img {
            transition: all 0.3s ease 0s;
          }
          &:hover {
            img {
              transform: translateY(-3px);
            }
          }
        }
        button:last-child {
          img {
            transition: all 0.3s ease 0s;
          }
          &:hover {
            img {
              transform: translateX(5px);
            }
          }
        }
      }
    }
    .exam__theory {
      + .exam__buttons {
          button:first-child {
          transform-origin: 50% 50%;
          img {
            transition: all 0.3s ease 0s;
          }
          &:hover {
            img {
              transform: rotate(-360deg);
            }
          }
        }
        button:last-child {
          img {
            transition: all 0.3s ease 0s;
          }
          &:hover {
            img {
              transform: translateX(-5px);
            }
          }
        }
      }
    }
    @media (max-width: 992px){
        flex: 0 1 100%;
    }
  }

  // .exam__posititon

  &__posititon {
    border: 1px solid #CDE3FF;
    border-radius: 10px;
    flex-grow: 1;
    width: 100%;
    position: relative;
    padding: 40px 60px 60px 30px;
    p {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 25px;
      color: var(--dark-color);
    }
    @media (max-width: 480px){
        padding: 30px 35px 30px 10px;
        p {
          font-size: 14px;
          line-height: 22px;
        }
    }
  }

  // .exam__btn-posit

  &__btn-posit {
    position: absolute;
    top: 20px;
    right: 20px;
    @media (max-width: 480px){
        top: 10px;
        right: 10px;
    }
  }

  // .exam__buttons

  &__buttons {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    @media (max-width: 400px){
        flex-direction: column;
        row-gap: 10px;
        button {
          width: 100%;
        }
    }
  }

  // .exam__btn

  &__btn {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    width: 160px;
    padding: 5px 0;
    > img {
      margin-right: 10px;
    }
    &-next {
      img {
        margin-right: auto;
        margin-left: 10px;
      }
    }
  }

  // .exam__asks

  &__asks {
    > ul {
      padding: 0;
      margin: 0;
      list-style: none;
      > li {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 25px;
        color: var(--dark-color);
        margin-bottom: 20px;
        @media (max-width: 480px){
            font-size: 14px;
            line-height: 22px;
            margin-bottom: 10px;
        }
      }
    }
  }
}

