// SUBSCRIBE PAGE START ========================================================================================================================================================

.subscribe {
  padding-bottom: 105px;
  // .subscribe__wrap

  &__wrap {
    column-gap: 85px;
    @media (max-width: 1200px){
      column-gap: 45px;
    }
  }

  // .subscribe__descr

  &__descr {
    max-width: 603px;
    @media (max-width: 1200px){
        max-width: 45%;
    }
    @media (max-width: 992px){
        max-width: 80%;
        align-self: flex-start;
    }
    @media (max-width: 768px){
        max-width: 100%;
        text-align: center;
    }
  }

  // .subscribe__title

  &__title {
    @media (max-width: 480px){
        font-size: 28px;
        line-height: 36px;
    }
  }

  // .subscribe__image

  &__image {
    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
      path.fig-1 {
        animation: opacity 0.7s ease-in-out 0s 8;
      }
      path.fig-3 {
        transform-origin: 90% 50%;
        animation: opacity 1.2s ease-in-out 1s 7;
      }
      path.notebook {
        animation: backgroundColor 3s ease-in-out 0s 3;
      }
      path.clock {
        animation: backgroundColorClock 3s ease-in-out 0s 3;
      }
      path.notebook-circle {
        animation: backgroundColorCircle 3s ease-in-out 0s 3 forwards;
      }
      path.translateX {
        animation: translateX 2s ease 0.7s 4;
      }
    }
    @media (max-width: 992px){
      text-align: center;
        svg {
          width: 80%;
        }
    }
  }
}

