        }
        @media (max-width: 1200px){
            flex: 0 1 30%;
            font-size: 15px;
            line-height: 35px;
        }
        @media (max-width: 768px){
            flex: 0 1 50%;
        }
        @media (max-width: 576px){
            font-size: 12px;
            line-height: 20px;
        }
        @media (max-width: 400px){
          font-size: 16px;
          line-height: 26px;
          flex: 0 1 100%;
          text-align: center;
      }
      }
    }
    @media (max-width: 1200px){
        padding: 40px 30px;
    }
    @media (max-width: 576px){
      padding: 15px;
    }
  }

  // .glossary__message

  &__message {
    margin-top: 30px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--dark-color);
    text-align: center;
    a {
      color: var(--accent-color);
      &:hover {
        text-decoration-line: underline;
      }
    }
    @media (max-width: 768px){
        font-size: 14px;
        line-height: 22px;
    }
  }

  // .glossary__inner

  &__inner {
    background-color: var(--light-color);
    border: 1px solid #CDE3FF;
    border-radius: 10px;
    padding: 40px 40px 120px 40px;
    min-height: 580px;
    position: relative;
    @media (max-width: 576px){
        padding: 20px 20px 80px 20px;
    }
  }

  // .glossary__name

  &__name {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 40px;
    color: var(--dark-color);
    margin-bottom: 20px;
  }

  // .glossary__text

  &__text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    color: var(--dark-color);
  }

  // .glossary__btn

  &__btn {
    position: absolute;
    bottom: 80px;
    left: 50%;
    transform: translateX(-50%);
    max-width: fit-content;
    padding-right: 75px;
    padding-left: 75px;
    cursor: pointer;
    @media (max-width: 768px){
        padding-right: 40px;
        padding-left: 40px;
    }
    @media (max-width: 576px){
        padding-right: 0;
        padding-left: 0;
        min-width: 60% !important;
        text-align: center;
        bottom: 20px;
    }
    @media (max-width: 400px){
        min-width: calc(100% - 30px) !important;
    }
  }

}

// GLOSSARY PAGE END ========================================================================================================================================================

// FOOTER START ========================================================================================================================================================

.footer {
  background-color: var(--dark-color);
  padding-top: 60px;

  // .footer__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;
    @media (max-width: 992px){
        flex-direction: column;
    }
    @media (max-width: 576px){
        padding-bottom: 10px;
    }
  }

  // .footer__logos

  &__logos {
    > ul {
      padding: 0;
      margin: 0;
      list-style: none;
      display: flex;
      margin-top: 40px;
      @media (max-width: 576px){
          justify-content: center;
      }
      > li {
        margin-right: 15px;
        width: 30px;
        height: 30px;
        &:last-child {
          margin-right: 0;
        }
        > a {
          display: block;
          width: 100%;
          height: 100%;
          transition: all 0.3s ease 0s;
          &:hover {
            transform: translateY(-6px);
          }
        }
      }
    }
    @media (max-width: 576px){
        text-align: center;
    }
  }

  // .footer__links

  &__links {
    display: flex;
    @media (max-width: 992px){
        justify-content: space-between;
        margin-top: 40px;
        max-width: 80%;
    }
    @media (max-width: 768px){
        max-width: 100%;
    }
    @media (max-width: 576px){
        flex-direction: column;
    }
  }

  // .footer__item

  &__item {
    margin-right: 141px;
    @media (max-width: 1200px){
        margin-right: 100px;
    }
    @media (max-width: 992px){
        margin-right: 0;
    }
    &:last-child {
      margin-right: 100px;
      @media (max-width: 991px){
          margin-right: 0;
      }
    }
    > ul {
      padding: 0;
      margin: 0;
      list-style: none;
      > li {
        margin-top: 5px;
        > a {
          font-family: 'Roboto';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          text-transform: uppercase;
          color: var(--light-color);
          position: relative;
          transition: all 0.3s ease 0s;
          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0px;
            width: 0%;
            height: 1px;
            background-color: var(--light-color);
            transition: all 0.3s ease 0s;
          }
          &:hover:after {
            width: 100%;
          }
        }
      }
    }
    &:last-of-type {
      > ul {
        > li {
          &:last-child {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--light-color);
            text-transform: none;
          }
          > a {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--light-color);
            text-transform: none;
          }
        }
      }
      @media (max-width: 768px){
          flex: 0 1 33.3333%;
      }
    }
    @media (max-width: 576px){
      text-align: center;
        > ul {
          margin-bottom: 30px;
        }
    }
  }

  // .footer__title

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--light-color);
  }

  // .footer__copyright

  &__copyright {
    border-top: 1px solid rgba(#ffffff, 0.1);
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 768px){
        flex-direction: column;
    }

  }

  // .footer__design

  &__design {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #AAAAAA;
    a {
      color: var(--light-color) !important;
      &:hover {
        color: var(--light-color) !important;
        text-decoration: underline !important;
      }
    }
    @media (max-width: 768px){
        margin-bottom: 7px;
        font-size: 12px;
        text-align: center;
    }

  }

  // .footer__policy

  &__policy {
    font-family: 'Roboto';
    a {
      display: inline-block;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light-color) !important;
      &:last-of-type {
        margin-left: 10px;
      }
      &:hover {
        color: var(--light-color) !important;
        text-decoration: underline !important;
      }
      @media (max-width: 768px){
          font-size: 12px;
      }
    }
  }
}

// FOOTER END ========================================================================================================================================================

// KEYFRAMES ========================================================================================================================================================

@keyframes rotateRound {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0);
  }
}

@keyframes translateX {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translate(8%);
  }
  100% {
    transform: translate(0);
  }
}

@keyframes backgroundColor {
  0% {
    fill: var(--light-color);
  }
  50% {
    fill: hsl(204, 71%, 81%);
  }
  100% {
    fill: var(--light-color);
  }
}

@keyframes backgroundColorCircle {
  0% {
    fill: #d8d4d4;
  }
  50% {
    fill: #256e9e;
  }
  100% {
    fill: #d8d4d4;
  }
}

@keyframes backgroundColorClock {
  0% {
    fill: #CDE3FF;
  }
  50% {
    fill: #8db9f1;
  }
  100% {
    fill: #CDE3FF;
  }
}

@keyframes backgroundColorBlueToYellow {
  0% {
    fill: #1F7DBB;
  }
  50% {
    fill: #FFC73C;
  }
  100% {
    fill: #1F7DBB;
  }
}

@keyframes backgroundColorYellowToBlue {
  0% {
    fill: #1F7DBB;
  }
  50% {
    fill: #FFC73C;
  }
  100% {
    fill: #1F7DBB;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(180deg);
  }
  50% {
    transform: rotate(360deg);
  }
  75% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes scale {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}


@keyframes opacity {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes opacitySlowly {
  0% {
    opacity: 0.4;
  }
  25% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}

