html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
h1, h2, h3, h4, h5 ,h6 {
  margin: 0;
  padding: 0;
}
body {
  &.lock {
    overflow: hidden;
  }
}
.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-outline {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--dark-color);
  border: 1px solid var(--accent-color);
  border-radius: 5px;
  padding: 8px 0;
  background-color: transparent;
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: var(--accent-color);
    color: var(--light-color);
  }
}

.btn {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--light-color);
  border: 1px solid var(--accent-color);
  background-color: var(--accent-color);
  border-radius: 5px;
  padding: 10px 0;
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: transparent;
    color: var(--dark-color);
    border: 1px solid var(--accent-color);
  }
}

.btn-back {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--accent-color);
  background-color: var(--light-color);
  padding: 10px 0;
  border: 1px solid #CDE3FF;
  border-radius: 5px;
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: #CDE3FF;
  }
  > img {
    margin-right: 10px;
  }
}

.title {
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 50px;
  color: var(--accent-color);
  @media (max-width: 768px){
      font-size: 35px;
      line-height: 45px;
  }
  @media (max-width: 576px){
    font-size: 30px;
    line-height: 40px;
  }
}

.cloud {
  width: 100%;
  height: 100vh;
  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}


.breadcrumb {
  display: flex;
  align-items: center;
}
.breadcrumb-item {
  a {
    color: var(--accent-color) !important;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    &:hover {
      text-decoration: underline;
    }
  }
  &.active {
    color: var(--accent-color) !important;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    display: block;
  }
  &:before {
    color: #CDE3FF !important;
  }
}
