:root {
  // base
  --font-family: "RobotoSlab", sans-serif;
  --content-width: 1350px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #ffffff;
  --dark-color: #263238;
  --accent-color: #1f7dbb;

    // Search

    --line-thickness: 1px;
    --glass-size: 65%;
    --icon-height: 2rem;
    --transition-speed: .45s;
    --timing-function: cubic-bezier(.66,1.51,.77,1.13);
    --icon-color: #263238;
}
